@import url("https://fonts.googleapis.com/css?family=Open+Sans:300,300i,400,400i,600,600i,700,700i,800,800i&subset=cyrillic,cyrillic-ext,latin-ext");
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-angle-right {
  width: 0.59em;
  height: 1em;
  fill: #C5CCD5;
}

.icon-logo {
  width: 0.98em;
  height: 1em;
  fill: #fff;
}

.icon-logout {
  width: 1em;
  height: 1em;
  fill: #FFF;
}

.icon-search {
  width: 1em;
  height: 1em;
  fill: #FFF;
}

body {
  min-width: 320px;
  background: #FFF;
  color: #002257;
  font-family: "Open Sans", sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 1.4;
}

.out {
  display: flex;
}

.menu,
.aside,
.main {
  overflow: auto;
  height: 100vh;
}

.menu {
  width: 244px;
}

.aside {
  width: 400px;
}

.main {
  flex-grow: 1;
}

.title-lg {
  color: #37394C;
  font-size: 24px;
  font-weight: 400;
  line-height: 33px;
}

.menu {
  padding: 45px 30px 65px;
  box-shadow: 0 2px 9px 0 rgba(0, 0, 0, 0.06);
  background: #47457e;
  background-image: linear-gradient(249deg, #47457e, #2e2d54);
}

.menu__profile {
  margin-bottom: 48px;
}

.menu__meta-title {
  text-transform: uppercase;
  color: #ffffff;
  font-size: 12px;
  font-weight: 600;
  line-height: 1.42;
  letter-spacing: .1px;
  margin-bottom: 19px;
}

.menu__meta-label {
  display: inline-block;
  color: rgba(255, 255, 255, 0.9);
  text-decoration: none;
  font-size: 13px;
  font-weight: 400;
  line-height: 1.7;
}

.menu__logout {
  position: absolute;
  left: 30px;
  bottom: 35px;
}

.menu__logout-label {
  display: inline-block;
  vertical-align: middle;
  color: #ffffff;
  font-size: 13px;
  line-height: 1.7;
}

.menu__logout-icon {
  display: inline-block;
  vertical-align: middle;
  margin-left: 7px;
  font-size: 15px;
}

.main {
  padding: 40px 32px 18px;
  background: #f8f9fb;
}

.main__inner {
  margin: 0 auto;
  max-width: 754px;
}

.main__inner:after {
  content: " ";
  display: table;
  clear: both;
}

.main__header {
  margin-bottom: 38px;
}

.main__header:after {
  content: " ";
  display: table;
  clear: both;
}

.main__title {
  float: left;
  padding: 2px 32px 5px 0;
}

.main__search {
  position: relative;
  float: left;
  padding-left: 33px;
  width: 100%;
  max-width: 408px;
}

.main__search:before {
  content: '';
  position: absolute;
  top: 4px;
  left: 0;
  bottom: 4px;
  border-left: 1px solid rgba(83, 82, 133, 0.06);
}

.search__input {
  padding-left: 54px;
}

.search__field {
  position: relative;
  box-shadow: -6px 7px 15px 0 rgba(58, 70, 93, 0.07);
}

.search__btn {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 54px;
  background: none;
  text-align: center;
  font-size: 0;
}

.search__btn:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.search__icon {
  display: inline-block;
  vertical-align: middle;
  fill: #4A5B6D;
  font-size: 18px;
}

.search__input {
  display: block;
  border-radius: 5px;
  width: 100%;
  height: 44px;
  color: #002257;
  background: #ffffff;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: normal;
}

.search__input::placeholder {
  opacity: 1;
  color: #849BB0;
}

.table {
  font-size: 0;
}

.table__th {
  display: inline-block;
  vertical-align: top;
  padding-bottom: 8px;
  color: #AAB2C0;
  font-size: 11px;
  font-weight: 600;
  line-height: 15px;
  text-transform: uppercase;
}

.table__th, .table__td {
  padding-left: 15px;
  padding-right: 15px;
}

.table__th:after, .table__td:after {
  content: " ";
  display: table;
  clear: both;
}

.table__th[data-column="name"], .table__td[data-column="name"] {
  padding-left: 18px;
  width: 27.85146%;
}

.table__th[data-column="frequency"], .table__td[data-column="frequency"] {
  width: 15.11936%;
}

.table__th[data-column="address"], .table__td[data-column="address"] {
  width: 41.77719%;
}

.table__th[data-column="status"], .table__td[data-column="status"] {
  padding-right: 60px;
  width: 15.25199%;
}

.table__td {
  display: inline-block;
  vertical-align: middle;
  font-size: 15px;
  font-weight: 400;
  line-height: 22px;
}

.table__item {
  position: relative;
  display: block;
  padding-top: 13px;
  padding-bottom: 17px;
  border: 1px solid #E6EAEE;
  border-radius: 4px;
  color: inherit;
  background: #ffffff;
  text-decoration: none;
  transition: border-color .2s, background .2s;
}

.table__item:not(:last-child) {
  margin-bottom: 8px;
}

.no-touch .table__item:hover {
  border-color: rgba(84, 90, 191, 0.58);
  background: #F6FBFD;
}

.table__img {
  float: left;
  margin-top: 3px;
  border-radius: 50%;
  width: 38px;
  height: 38px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.table__content {
  overflow: hidden;
  padding-left: 18px;
}

.table__title {
  font-size: 15px;
  font-weight: 600;
  line-height: 22px;
}

.table__meta {
  color: #97A3B4;
  font-size: 13px;
  font-weight: 400;
  line-height: 18px;
}

.table__arrow {
  position: absolute;
  top: 50%;
  right: 29px;
  margin-top: -0.5em;
  fill: #B1BAC7;
  font-size: 17px;
}

.table__status {
  display: block;
  margin: 0 auto;
  border: 2px solid #FFFFFF;
  border-radius: 50%;
  width: 12px;
  height: 12px;
  background: #59C78A;
  box-shadow: 0 0 0 1px #59C78A;
}

.table__status.is-error {
  background: #F05692;
  box-shadow: 0 0 0 1px #F05692;
}

.table__label {
  display: none;
}

.profile__container {
  display: block;
  text-decoration: none;
}

.profile__container:after {
  content: " ";
  display: table;
  clear: both;
}

.profile__figure {
  float: left;
  width: 64px;
  height: 64px;
  text-align: center;
  font-size: 0;
}

.profile__figure:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.profile__logo {
  font-size: 64px;
  display: inline-block;
  vertical-align: middle;
}

.profile__img {
  border-radius: 50%;
  background-position: center center;
  background-size: cover;
}

.profile__content {
  margin-left: 64px;
  padding: 10px 0 15px 25px;
}

.profile__title {
  position: relative;
  display: inline-block;
  color: #ffffff;
  margin-bottom: 5px;
  padding-right: 14px;
  font-size: 15px;
  font-weight: 600;
  line-height: 1.34;
}

.profile__title:before {
  content: '';
  position: absolute;
  top: 8px;
  right: 0;
  width: 6px;
  height: 6px;
  border-radius: 6px;
  background: #f05692;
}

.profile__title.is-active:before {
  background: #2ebd41;
}

.profile__meta {
  color: #c0c9d7;
  font-size: 12px;
  line-height: 1.42;
}
