@mixin col($name) {
	&[data-column="#{$name}"] {
		@content; } }

$colName: 'name';
$colFruquency: 'frequency';
$colAddress: 'address';
$colStatus: 'status';

$tableWidth: 7.54;

.table {
	font-size: 0;
	&__th {
		display: inline-block;
		vertical-align: top;
		padding-bottom: 8px;
		color: #AAB2C0;
		@include f(11px, 600, 15px);
		text-transform: uppercase; }
	&__th,
	&__td {
		padding-left: 15px;
		padding-right: 15px;
		@include clr;
		@include col($colName) {
			padding-left: 18px;
			width: (210/$tableWidth) + %; }
		@include col($colFruquency) {
			width: (114/$tableWidth) + %; }
		@include col($colAddress) {
			width: (315/$tableWidth) + %; }
		@include col($colStatus) {
			padding-right: 60px;
			width: (115/$tableWidth) + %; } }
	&__td {
		display: inline-block;
		vertical-align: middle;
		@include f(15px, 400, 22px); }
	&__item {
		position: relative;
		display: block;
		padding-top: 13px;
		padding-bottom: 17px;
		border: 1px solid #E6EAEE;
		border-radius: 4px;
		color: inherit;
		background: $white;
		text-decoration: none;
		transition: border-color .2s, background .2s;
		&:not(:last-child) {
			margin-bottom: 8px; }
		@include hover {
			border-color: rgba(84,90,191,0.58);
			background: #F6FBFD; } }
	&__img {
		float: left;
		margin-top: 3px;
		border-radius: 50%;
		width: 38px;
		height: 38px;
		@include cover; }
	&__content {
		overflow: hidden;
		padding-left: 18px; }
	&__title {
		@include f(15px, 600, 22px); }
	&__meta {
		color: #97A3B4;
		@include f(13px, 400, 18px); }
	&__arrow {
		position: absolute;
		top: 50%;
		right: 29px;
		margin-top: -0.5em;
		fill: #B1BAC7;
		font-size: 17px; }
	&__status {
		display: block;
		margin: 0 auto;
		border: 2px solid #FFFFFF;
		border-radius: 50%;
		width: 12px;
		height: 12px;
		background: #59C78A;
		box-shadow: 0 0 0 1px #59C78A;
		&.is-error {
			background: #F05692;
			box-shadow: 0 0 0 1px #F05692; } }
	&__label {
		display: none; } }
